import React, { Component } from 'react'
import './fonts/zig.ttf'
import './css/games.css'
import { DrawGame } from './DrawGame'
import GameData from './GameData'
import { Table, Container, Row, Col } from 'react-bootstrap'
import {withRouter} from 'react-router';



console.log(GameData)

const defaultText = (
<div>
  <h2>Welcome to the game area</h2>
  <p>Here you can find all the games.</p>
</div>
)

class Games extends Component {
    windowWidth;
    constructor() {
      super()
      this.windowWidth = window.innerWidth
      this.state = { gameInfo: defaultText, hideSidebar: false }
    }
    showInfo(data) {
      this.setState({ gameInfo: 
        <div>
          <img alt=""  width="80%"
            src={`${process.env.PUBLIC_URL}/logos/` + data.groupid + `_pelilogo.png`} 
            />
          <h2>{data.gameName}</h2>
          <p>{data.description}</p>
        </div>
      })
    }
    componentDidMount() {
      if (this.windowWidth < 768 ) {
        this.setState({hideSidebar: true})
      } else {
        this.setState({hideSidebar: false})
      }
    }
    render() {
      let games = Object.keys(GameData).map(game => 
        
        <tr width="100%" style={{color: 'white'}} key={GameData[game].groupid} onClick={() => window.location.href=GameData[game].url}>
          
          <td width="30%"> 
            <img className="gameTableImg" alt="" 
                src={`${process.env.PUBLIC_URL}/logos/` + GameData[game].groupid + `_pelilogo.png`} 
                />
          </td>
          <td width="70%" className="gameTableTitle">
              <button>{GameData[game].gameName} </button>
          </td>
          
        </tr>
        
      )
      return (
          
<Container fluid className="containerPadding gameMain" id="games">
<Row>
  <Col sm={7}>
  <h2 className="title2" onMouseEnter={() => this.setState({gameInfo: defaultText})}>The Games</h2>
  <section>  
    <div className="row">
      <DrawGame gameObj={GameData.game19tiko1a} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko1b} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko1c} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko1d} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
    </div>
    <div className="row">
      <DrawGame gameObj={GameData.game19tiko1e} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko1f} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko2a} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
    </div>
    <div className="row">
      <DrawGame gameObj={GameData.game19tiko2b} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko2c} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko2d} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko2e} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
    </div>
    <div className="row">
      <DrawGame gameObj={GameData.game19tiko2f} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko3a} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko3b} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
    </div>
    <div className="row">
      <DrawGame gameObj={GameData.game19tiko3c} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko3d} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko3e} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
      <DrawGame gameObj={GameData.game19tiko3f} showInfo={this.showInfo.bind(this)} windowWidth={this.windowWidth} />
    </div>
  </section>
  </Col>
  <Col sm={5} className={this.state.hideSidebar ? 'hidden' : 'gameInfo'} >
      <h2>{this.state.gameInfo}</h2>
  </Col>
</Row>
<Row>
<Table striped hover variant="dark" className={this.state.hideSidebar ? '' : 'hidden'}>
  <thead>
    <tr className="tableHeader">
      <th className="tableHeaderLogo">Logo</th>
      <th>Title</th>
    </tr>
  </thead>
  <tbody>{games}</tbody>
</Table>
</Row>
</Container>
        )
    }
}

export default withRouter(Games)